	module.exports = {"data":{"post":{"html":"<p>This year we have the luck that our McDonald's spring meeting in Munich coincides with Christopher's Easter vacation at the end of March. So he gets to come along! Christopher also has the luck that Legoland Germany opens that same week.</p>\n<p>The meeting ends on Tuesday, and Legoland doesn't open until Saturday, so we have 3 days to explore Munich and surroundings. We know about the <a href=\"http://www.deutsches-museum.de/en/information/\">Deutsches Museum</a>, and we might take a day trip to the <a href=\"http://www.zugspitze.de/main_en.php\">Zugspitze</a>. Are there any other family activities in Munich that can be recommended?</p>","fields":{"title":"Spring Break","date":"08 Mar 2009","url":"https://papascott.de/2009-03-08-spring-break/"}}},"pathContext":{"slug":"/2009-03-08-spring-break/","prev":{"fields":{"title":"Journalist Christopher","date":"18 Feb 2009","slug":"/2009-02-18-journalist-christopher/"}},"next":{"fields":{"title":"Irish Crab","date":"14 Mar 2009","slug":"/2009-03-14-irish-crab/"}}}}

